p.btn_black {
	margin: 0;
	display: inline-block;
	overflow: hidden;
	border-radius: 22px;
	background-color: #262222;
	box-shadow: 3px 3px 0px #fed3bf;
	position: relative;
	min-width: 230px;
}
p.btn_black span {
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0;
	left: 0;
	z-index: 1;
	background-color: #fed3bf;
	opacity: 0;
	transition: opacity 300ms ease-in-out;
}
p.btn_black a {
	color: #fed3bf;
	font-weight: 600;
	width: 100%;
	padding: 10px 50px;
	position: relative;
	z-index: 2;
	text-transform: uppercase;
	transition: all 300ms ease-in-out;
	text-align: center;
	min-width: 180px;
	cursor: pointer;
}
p.btn_black:hover span {
	opacity: 0.1;
}
p.btn_clicked a {
	font-size: 16px;
	padding: 6px 50px;
}

.tab_container_outer {
	width: 100%;
	overflow: hidden;
	overflow-x: scroll;
}
.tab_container_outer::-webkit-scrollbar {
	display: none;
}
.tab_container_inner {
	min-width: 100%;
}
.tab_item {
	text-align: center;
	display: inline-block;
	padding: 15px 0;
	margin-left: 30px;
	border-bottom: 3px solid rgba(0, 0, 0, 0);
	transition: all 300ms ease-in-out;
}
.tab_container_inner .tab_item:nth-child(1) {
	margin-left: 0px;
}
.tab_item.active,
.tab_item:hover {
	border-bottom: 3px solid #fed3bf;
}
.tab_item p {
	cursor: pointer;
}
.tab_item.active a,
.tab_item.active p {
	font-weight: 600;
}

.input_item {
	display: inline-block;
	position: relative;
}
.input_item .select_element,
ul.options_container li {
	cursor: pointer;
}
.input_item .select_element,
.input_item input,
.input_item textarea {
	display: inline-block;
	overflow: hidden;
	border-radius: 22px;
	padding: 10px 30px;
	background-color: #ffffff;
	border: 1px solid #9a9a9a;
	font-size: 22px;
	line-height: 30px;
	transition: all 300ms ease-in-out;
	min-width: 330px;
	margin: 0;
	position: relative;
}
.input_item input:hover,
.input_item textarea:hover,
.input_item .select_element:hover {
	border-color: #000000;
}
.input_item input:focus-visible,
.input_item textarea:focus-visible {
	outline: none;
	border-color: #3b8dca;
}
.input_item input.validation_success,
.input_item textarea.validation_success,
.input_item .select_element.validation_success {
	border-color: #9a9a9a;
	color: #3b8dca;
}
.input_item input.validation_error,
.input_item textarea.validation_error,
.input_item .select_element.validation_error {
	border-color: #be090a;
	color: #be090a;
}
.input_item input:focus-visible,
.input_item input.validation_success:focus-visible,
.input_item input.validation_error:focus-visible,
.input_item textarea:focus-visible,
.input_item textarea.validation_success:focus-visible,
.input_item textarea.validation_error:focus-visible {
	color: #000000;
}
.input_item textarea {
	height: 160px;
}
span.validation_error_message {
	padding: 10px 30px;
}
ul.options_container {
	border: none;
	border-top: none;
	border-radius: 0 0 22px 22px;
	overflow: hidden;
	height: 0;
	width: 100%;
	transition: height 300ms ease-in-out;
	position: absolute;
	background-color: #ffffff;
	z-index: 1;
	text-align: left;
}
p.select_element.rotateArrow_180 + ul.select_options.options_container {
	border: 1px solid #9a9a9a;
}
ul.options_container li {
	padding: 10px 30px;
	transition: all 300ms ease-in-out;
}
ul.options_container li.active,
ul.options_container li.active:hover {
	background-color: #3b8dca;
	color: #ffffff;
}
ul.options_container li:hover {
	background-color: #f7f7f7;
}
p.select_element::after {
	content: "";
	position: absolute;
	top: 0;
	right: 20px;
	background-image: url(../Images/Arrow.png);
	height: 100%;
	width: 15px;
	background-position: center;
	background-size: 15px auto;
	background-repeat: no-repeat;
	transition: all 300ms ease-in-out;
}
p.select_element.rotateArrow_180::after {
	transform: rotate(180deg);
}
p.select_element.rotateArrow_180 {
	border-bottom: none;
	border-radius: 22px 22px 0 0;
}
select.select_input {
	display: none;
}

